import React, { useRef, useEffect } from "react";
import { graphql } from "gatsby";
import Layout from "./layout-page";
import Header from "../components/header";
import Footer from "../components/footer";
import PostsList from "../components/postsList";

/**
 * QUery
 */
export const pageQuery = graphql`
  query categoryPageQuery($category: String!) {
    allMdx(
      sort: { fields: [frontmatter___date], order: ASC }
      filter: {
        frontmatter: { category: { eq: $category } }
        fields: { group: { eq: "blog" } }
      }
    ) {
      nodes {
        frontmatter {
          title
          date
          category
          cover {
            childImageSharp {
              gatsbyImageData(layout: FULL_WIDTH)
            }
          }
        }
        fields {
          slug
          group
        }
        excerpt(pruneLength: 100)
      }
    }
  }
`;

/**
 * View
 * @param {object} props
 */
export default function Template({ data, pageContext }) {
  // format mdx data
  const posts = data?.allMdx?.nodes || null;

  const pageTitle = pageContext?.category || null;

  return (
    <Layout>
      <div className="container pt-2 mt-4 mb-3">
        {pageTitle && (
          <h1 className="mb-4 effect-square">
            Cat√©gorie : {pageTitle}
          </h1>
        )}
        {posts.length > 0 && (
          <PostsList posts={posts} />
        )}
      </div>
    </Layout>
  );
}
